header {
    position: relative;
    padding: 48px 0;

    >.container {
        --gap: 94px;
        display: flex;
        align-items: center;
        gap: var(--gap);
    }

    .btn-burger {
        margin-left: auto;
    }
}

.header-logo {
    img {
        max-width: 195px;
        min-height: 41px;
    }
}

.mobile-container {
    display: flex;
    align-items: center;
    gap: var(--gap);
    width: 100%;

    .btn {
        margin-left: auto;
        padding: 10px 20px;
        font-size: 20px;
        border: none;
    }
}

.menu {
    &__list {
        display: flex;
        gap: var(--gap);
    }

    &__link {
        font-size: 20px;
        color: $text-color;
        text-decoration: none;
    }
}

.btn-burger {
    position: relative;
    z-index: 1;
    width: 42px;
    height: 18px;
    background: none;
    border: none;

    span {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 2px;
        background-image: linear-gradient(245deg, #237249 0%, #35c66b 100%);
        transition: .5s;

        &:nth-child(2) {
            top: 8px;
        }

        &:last-child {
            top: 16px;
        }
    }

    &.active {
        span {
            top: 7px;
            transform: rotate(45deg);

            &:nth-child(2) {
                top: 7px;
                transform: rotate(-45deg);
            }

            &:last-child {
                opacity: 0;
            }
        }
    }
}
